* {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
span a {
  background: #465b86;
  color: #fff;
  border-radius: 10px 0 10px 0;
  padding: 2px 15px;
  text-decoration: none
}
body {
  background: #808895;
  font: 14px 黑体;
  color: #465b86;
  line-height: 1.2;
}
.iconfont {
  background: #465b86;
  border-radius: 50%;
  padding: 4px;
  margin-right: 10px;
  color: #fff;
}
.container {
  width: 1000px;
  background: #e8e9ee;
  margin: 50px auto;
  display: flex;
}

.left {
  flex: 2;
  font-weight: bold;
  width: 250px;
  display: inline-block;
  height: 100%;
  text-align: center;
  height: 100%;
}

.logo img {
  border-radius: 50%;
  margin-top: 80px;
}

.left .name {
  margin-top: 20px;
  padding: 20px;
  color: #fff;
  background: #465d7f;
}

.left .info {
  margin-top: 20px;
  padding: 10px;
  text-align: left;
}

.info ul li {
  padding: 10px;
}

.right {
  flex: 5;
  padding: 20px 50px 50px 20px;
  padding-top: 60px;
  background: #fff;
}

.right section {
  padding-bottom: 20px;
}

section h1 {
  padding: 20px 0;
}

section h3 {
  padding: 4px 0;
  margin-left: 10px;
}

section li {
  padding: 2px;
  list-style: square;
  margin-left: 25px;
  color: #333;
}

.common li {
  list-style: none;
  margin-left: 10px;
}

li span {
  font-weight: bold;
  color: #465b86;
}

.link {
  margin-top: 30px;
}

.link h2 {
  background: #fff;
  border: 1px solid #465b86;
  display: inline-block;
  font-size: 16px;
  padding: 5px 20px;
  border-radius: 10px 0 10px 0;
}

.link li {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.link a {
  text-decoration: none;
  border-bottom: 2px dashed #465b86;
  color: #465b86;
  padding: 5px 0;
}
.link ul {
  margin-top: 30px;
}

.link .iconfont {
  font-size: 40px;
  height: 42px;
  width: 40px;
  background: none;
  margin-left: -10px;
  color: #465b86;
}
